/**
 * components/nav-toggle
 * =====================
 */

 
 .nav-toggle{
    
    &__btn{
        display: block;
        width: 100%;
        height: 100%;
    }
    &__icon{
        display:none;
        margin: 0 auto;

        &--is-visible{
            display: block;
        }
    }
}


