/**
 * components/article-card
 * =======================
 */

.article-card{
    max-width: 350px;
    margin: 0 auto;
    box-shadow: 0 0 8px -5px rgba(#333, .8) ;
    border-radius: 8px;
    background-color: $color-white;
    overflow: hidden;

    &__image{
        position: relative;
        padding-bottom: #{"min(60%, 180px)"};
        // max-height: 20px;
        img{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__content{
        padding: 8% 8% 14% 8%;
        text-align: left;
    }
    &__author{
        font-size: $font-size-xxs;
        line-height: $line-height-xxs;
    }
    &__title{
        margin-top: 8px;
        color: #333;
    }
    &__copy{
        margin-top: 12px;
        font-size: $font-size-xs;
        line-height: $line-height-xs;
    }

    @include bp($xl){
        &__image{
            padding-bottom: 75%;
        }
    }

}